@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;400&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
}

body {
  background-attachment: fixed;
  background-image: linear-gradient(to top, #fff1eb 0%, #ace0f9 100%);
}
.text--center {
  text-align: center;
}

.weather-app__list {
  margin-top: 50px;
  margin-bottom: 50px;
  list-style: none;
}
.weather-app__header {
  padding-top: 30px;
  padding-bottom: 30px;
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
}

.weather-app__header-title {
  font-size: 35px;
}

.weather-app__header-subtitle {
  font-size: 14px;
  opacity: 0.8;
}
.weather-app__search {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.8);
  padding-bottom: 10px;
}

.weather-app__search-input {
  background-color: rgba(255, 255, 255, 0);
  border: none;
  padding: 5px;
  font-size: 20px;
  opacity: 0.5;
  color: #fff;
}

.weather-app__search-input:hover {
  opacity: 1;
}

.weather-app__search-input:focus {
  opacity: 1;
  outline: none;
  outline-style: solid;
  outline-width: 1px;
  outline-color: rgba(255, 255, 255, 0.4);
}

.weather-app__search-input::-webkit-input-placeholder {
  color: white;
}

.weather-app__search-input:-ms-input-placeholder {
  color: white;
}

.weather-app__search-input::placeholder {
  color: white;
}

.weather-app__search-button {
  border: none;
  background-color: rgba(255, 255, 255, 0);
  font-size: 20px;
  padding: 5px;
  color: white;
  opacity: 0.7;
}

.weather-app__search-button:hover {
  cursor: pointer;
  opacity: 1;
}

.weather-app__search-button:focus {
  opacity: 1;
  outline: none;
}
.error-card__image-container {
  color: #fff;
  max-width: 600px;
  width: 80%;
  margin: auto;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 15px;

  background-image: url(https://wpklik.com/wp-content/uploads/2019/03/A-404-Page-Best-Practices-and-Design-Inspiration.jpg);
  background-position: center;
  background-size: cover;
}

.error-card__container {
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 15px;
  padding: 50px 50px;
}


.error-card__error-icon {
  opacity: 0.7;
  font-size: 30px;
}

.error-card__status-code {
  font-size: 26px;
}

.error-card__status-message {
  font-size: 20px;
  opacity: 0.7;
  text-transform: capitalize;
}

.error-card__status-message-guide {
  margin-top: 10px;
  font-size: 18px;
}
.list-item__card-nav {
  display: -webkit-flex;
  display: flex;
  border-bottom: solid 2px rgba(238, 238, 238, 0.5);
  -webkit-justify-content: center;
          justify-content: center;
}

@media only screen and (max-width: 380px) {
  .list-item__card-nav {
    border: none;
  }
}

.card-nav__item {
  color: rgba(238, 238, 238, 0.5);
  background-color: rgba(238, 238, 238, 0);
  border: none;
  margin: 0 10px;
  padding-left: 5px;
  padding-right: 5px;
  font-size: 16px;
}

.card-nav__item:hover {
  color: #FFF;
  cursor: pointer;
  border-bottom: solid 2px #FFF;
  margin-bottom: -2px;
}

.card-nav__item:focus {
  color: #FFF;
  border-bottom: solid 2px #FFF;
  margin-bottom: -2px;
  outline: none;
}

.item--active {
  color: #FFF;
  border-bottom: solid 2px #FFF;
  margin-bottom: -2px;
}
.list-item__extra-info {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-top: 20px;
  overflow: scroll;
  -webkit-flex-direction: column;
          flex-direction: column;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.list-item__extra-info::-webkit-scrollbar {
  display: none;
}

@media only screen and (min-width: 600px) {
  .list-item__extra-info {
    -webkit-flex-direction: row;
            flex-direction: row;
  }
}

.extra-info__card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px 0;
}

@media only screen and (min-width: 600px) {
  .extra-info__card {
    padding: 0;
  }
}

.extra-info__card h3 {
  font-size: 20px;
}

.extra-info__card h4 {
  font-size: 14px;
  opacity: 0.8;
}
.list-item__daily-container {
  overflow: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.list-item__daily-container::-webkit-scrollbar {
  display: none;
}

.list-item__daily-container::after {
  content: ""; display: table; clear: both;
}

.list-item__daily-info {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-top: 20px;
  float: center;
}

@media only screen and (max-width: 610px) {
  .list-item__daily-info {
    float: left;
  }
}

.daily-info__card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  min-width: 80px;
}

.daily-info__card h3 {
  font-size: 20px;
}

.daily-info__card h4 {
  font-size: 16px;
}

.daily-info__card h5 {
  font-size: 14px;
  opacity: 0.8;
}

.daily-info__card h6 {
  font-size: 13px;
  opacity: 0.8;
}
.list-item__hourly-container {
  overflow: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.list-item__hourly-container::-webkit-scrollbar {
  display: none;
}

.list-item__hourly-container::after {
  content: ""; display: table; clear: both;
}

.list-item__hourly-info {
  display: -webkit-flex;
  display: flex;
  margin-top: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  float: center;
}

@media only screen and (max-width: 610px) {
  .list-item__hourly-info {
    float: left;
  }
}

.hourly-info__card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.hourly-info__card h4 {
  font-size: 16px;
}

.hourly-info__card h5 {
  font-size: 14px;
  opacity: 0.8;
}
